import categories from "./categories.json";
import controls from "./controls.json";
import catalog from "./catalog.json";


const sprintf = require('sprintf-js').sprintf;
const clonedeep = require('lodash.clonedeep');
const pako = require('pako');



function getPresetBlocksOrdered(hlx, dsp = 'dsp0', topology = '@topology0') {
    let pos_path0 = {};
    let pos_path1 = {};
    for (let b in hlx.data.tone[dsp]) {
        if (b.includes('block')) {
            let block = hlx.data.tone[dsp][b];
            let b_pos = block['@position'];
            if (block['@path'] == 0) {
                pos_path0[b_pos] = b;
            } else if (block['@path'] == 1) {
                pos_path1[b_pos] = b;
            }
        }
    }

    let topology_str = hlx.data.tone.global[topology];

    let pre_split = [];
    let split_pos = undefined;
    if (topology_str.includes('S')) {
        let split = getBlock(hlx, 'split', dsp);
        split_pos = split['@position'];
        // blocks before split, only path0
        for (let p in pos_path0) {
            if (p < split_pos) {
                pre_split.push(pos_path0[p]);
            }
        }
    }
    let post_join = [];
    let join_pos = undefined;
    if (topology_str.includes('J')) {
        let join = getBlock(hlx, 'join', dsp);
        join_pos = join['@position'];
        // bloks after join, only path0
        for (let p in pos_path0) {
            if (p >= join_pos) {
                post_join.push(pos_path0[p]);
            }
        }
    }
    let dual_path = {};
    if (split_pos != undefined && join_pos != undefined) {
        // between split and join
        for (let p in pos_path0) {
            if (p >= split_pos && p < join_pos) {
                dual_path[p] = pos_path0[p];
            }
        }
    } else if (split_pos != undefined && join_pos == undefined) {
        // after split till end
        for (let p in pos_path0) {
            if (p >= split_pos) {
                dual_path[p] = pos_path0[p];
            }
        }
    } else if (split_pos == undefined && join_pos != undefined) {
        // from start till join
        for (let p in pos_path0) {
            if (p < join_pos) {
                dual_path[p] = pos_path0[p];
            }
        }
    } else if (split_pos == undefined && join_pos == undefined
        && Object.keys(pos_path1).length > 0) {
        // no split no join but dual path
        // from start till end
        for (let p in pos_path0) {
            dual_path[p] = pos_path0[p];
        }
    }

    let between_split_join = [];
    // combine dual_path (path0) and path1
    if (split_pos != undefined || join_pos != undefined || topology_str.includes('AB')) {
        for (let p in pos_path1) {
            let pn = Number(p) + 0.5;
            dual_path[pn] = pos_path1[p];
        }
        for (let p of Object.keys(dual_path).sort()) {
            between_split_join.push(dual_path[p]);
        }
    }
    let blocks = [];
    if (Object.keys(pos_path1).length == 0) {
        for (let p of Object.keys(pos_path0).sort()) {
            blocks.push(pos_path0[p]);
        }
    } else {
        if (split_pos != undefined) {
            blocks = blocks.concat(pre_split, 'split');
        }
        if (between_split_join.length > 0) {
            blocks = blocks.concat(between_split_join);
        }
        if (join_pos != undefined) {
            blocks = blocks.concat('join', post_join);
        }
    }

    blocks.unshift('inputA');
    if (topology_str.includes('AB') && !topology_str.includes('S')) {
        blocks.unshift('inputB');
    }
    blocks.push('outputA');
    if (topology_str.includes('AB') && !topology_str.includes('J')) {
        blocks.push('outputB');
    }
    for (let b in hlx.data.tone[dsp]) {
        if (b.includes('cab')) {
            let block = hlx.data.tone[dsp][b];
            blocks.push(b);
        }
    }

    return blocks;
}

function getBlock(hlx, block_name, dsp = 'dsp0') {
    return hlx.data.tone[dsp][block_name];
}

function getBlockModelName(block, catalog) {
    let model_data = getModelData(catalog, block['@model']);;
    return model_data.name;
}

function getBlockCategory(block, catalog) {
    let model_data = getModelData(catalog, block['@model']);;
    return categories[model_data.category];
}



function getModelData(catalog, model_name) {
    let model_data = undefined;
    for (let category in catalog) {
        if (Object.keys(catalog[category].models).includes(model_name)) {
            model_data = catalog[category].models[model_name];
        }
    }
    return model_data;
}

function getModelParamsNames(model_data) {
    let params = [];
    for (let param of model_data.params) {
        params.push(param['symbolicID']);
    }
    return params;
}

function getParamData(model_data, param_name) {
    let param_data;
    for (let param of model_data.params) {
        if (param['symbolicID'] == param_name) {
            param_data = param;
        }
    }
    return param_data;
}

function getBlockParamDisplayName(block, param_name, catalog) {
    let model_data = getModelData(catalog, block['@model']);
    let param_data = getParamData(model_data, param_name);
    return param_data['name'];
}

function getBlockParamsValues(hlx, block, catalog, controls) {
	let params_values = {}
	let model_data = getModelData(catalog, block['@model'])
	let model_params = getModelParamsNames(model_data)

	for (let pname of model_params) {
		let param_display_name = getBlockParamDisplayName(block, pname, catalog)

		if (!pname.includes('@') || pname == '@input' || pname == '@output'
			|| pname == '@trails' || pname == '@mic') {

			let display_value = getBlockParamDisplayText(block, block[pname], pname, catalog, controls)

			if(display_value!=undefined) {
				params_values[param_display_name] = display_value
			}
		} else if (pname == '@stereo') {
			params_values[param_display_name] = block[pname]
		}
	}
	return params_values
}

function getControlDisplayType(controls, control_type) {
    let display_info = controls[control_type];
    if (display_info['alias'] != undefined) {
        display_info = controls[display_info['alias']];
    }
    return display_info;
}

function getBlockParamDisplayText(block, param_value, param_name, catalog, controls) {
    let model_data = getModelData(catalog, block['@model']);
    let param_data = getParamData(model_data, param_name);
    let display_info = getControlDisplayType(controls, param_data.displayType);

    if (param_data['stereo-only'] && !block['@stereo']) {
        return undefined;
    }

    let display_text = "";
    if (display_info.isDiscrete) {
        if (display_info.format == undefined) {
            display_text = "" + param_value;
        } else if (display_info.format instanceof Array) {
            if (display_info.format[0] instanceof Object) {
                for (let f of display_info.format) {
                    if (f.lowerBound != undefined && f.upperBound != undefined) {
                        if (param_value >= f.lowerBound && param_value < f.upperBound) {
                            display_text = sprintf(f.format, param_value);
                            break;
                        } else {
                            console.log('getBlockParamDisplayText: ' + param_value + " NOT between");
                        }
                    } else {
                        display_text = param_value;
                    }
                }
            } else {
                let format_position = param_value;
                if (param_value === true) {
                    format_position = 1;
                } else if (param_value === false) {
                    format_position = 0;
                }
                display_text = display_info.format[format_position];
            }

            if (!display_text) {
                display_text = "" + param_value;
            }
            if (display_text == "-") {
                display_text = param_value;
            }
        } else {
            let scale = display_info.dspToDisplayScale || 1;
            let format = display_info.format;
            if (display_info.formatUnits) format = display_info.formatUnits;
            display_text = sprintf(format, param_value * scale);
        }
    } else {
        let vt = param_data.valueType;
        let unitsMultiplier = 1;
        let format = "";
        if (display_info.format instanceof Array) {

            /*
            if(param_name == 'Tilt') {
                let scaled = ((-100*(1-param_value)) + (100*param_value)) / 1
                param_value = scaled
                if(scaled < -0.5) {
                    vt = 0
                } else if (scaled > 0.5) {
                    vt = 2
                } else {
                    vt = 1
                }
            } else 
            */

            if (display_info.format[0].lowerBound != undefined) {

                if (param_data.min != undefined && display_info.minimumValue != undefined) {
                    let x0 = param_data.min;
                    let x1 = param_data.max;

                    let y0 = display_info.minimumValue;
                    let y1 = display_info.maximumValue;

                    let x = param_value;
                    let y = ((y0 * (x1 - x)) + (y1 * (x - x0))) / (x1 - x0);
                    param_value = y;
                }

                if (display_info.dspToDisplayScale != undefined) {
                    param_value = param_value * display_info.dspToDisplayScale;
                }

                for (let f of display_info.format) {
                    if (param_value >= f.lowerBound && param_value <= f.upperBound) {
                        format = f.format;
                        if (f.formatUnits != undefined) {
                            format = f.formatUnits;
                        }
                        if (f.unitsMultiplier != undefined) {
                            param_value = param_value * f.unitsMultiplier;
                        }
                        break;
                    }
                }
            } else {
                format = display_info.format[vt].formatUnits;
                if (display_info.format[vt].unitsMultiplier) {
                    unitsMultiplier = display_info.format[vt].unitsMultiplier;
                }
            }

        } else {
            format = display_info.format;
            if (display_info.dspToDisplayScale != undefined) {
                param_value = param_value * display_info.dspToDisplayScale;
            }
            if (display_info.formatUnits) {
                format = display_info.formatUnits;
            }
        }
        display_text = sprintf(format, param_value * unitsMultiplier);
    }

    return display_text;
}

function getSnapshotNames(hlx) {
    let snapshot_names = [];
    for (let ssn in hlx.data.tone) {
        if (ssn.includes('snapshot')) {
            snapshot_names.push(ssn);
        }
    }
    return snapshot_names.sort();
}

function buildPresetDisplayModel(hlx, dsp = 'dsp0', topology = '@topology0') {
    let preset = {};
    preset['name'] = hlx.data.meta.name;
    preset['topology'] = hlx.data.tone.global[topology];
    preset['blocks'] = [];
    let blocks = getPresetBlocksOrdered(hlx, dsp, topology);
    let dualpath = false;
    if (blocks.includes('inputB')) {
        dualpath = true;
    }
    for (let bname of blocks) {
        let block = getBlock(hlx, bname, dsp);
        let block_data = {};
        block_data['name'] = getBlockModelName(block, catalog);
        block_data['category'] = getBlockCategory(block, catalog);
        block_data['path'] = block['@path'];
        block_data['fillpath'] = undefined;
        if (dualpath) {
            if (block_data['path'] == 0) {
                block_data['fillpath'] = 1;
            } else {
                block_data['fillpath'] = 0;
            }
        }
        block_data['dualpath'] = dualpath;
        block_data['params'] = getBlockParamsValues(hlx, block, catalog, controls);
        if (block['@cab']) {
            block_data['cab'] = block['@cab'];
        }
        block_data['enabled'] = block['@enabled'];
        block_data['block'] = bname;
        preset['blocks'].push(block_data);
        if (bname == 'split') {
            dualpath = true;
        } else if (bname == 'join') {
            dualpath = false;
        }
    }

    let snapshot_names = getSnapshotNames(hlx);

    preset['snapshots'] = {};
    for (let ss_name of snapshot_names) {
        preset['snapshots'][ss_name] = {};
        if (hlx.data.tone[ss_name].blocks != undefined) {
            preset['snapshots'][ss_name]['blocks'] = hlx.data.tone[ss_name].blocks[dsp];
            if (hlx.data.tone[ss_name].controllers != undefined) {
                let ss_b = hlx.data.tone[ss_name].controllers[dsp];
                preset['snapshots'][ss_name]['params'] = {};
                for (let bname in ss_b) {
                    let block = getBlock(hlx, bname, dsp);
                    preset['snapshots'][ss_name]['params'][bname] = {};
                    for (let pname in ss_b[bname]) {
                        let ss_p_v = ss_b[bname][pname]['@value'];
                        let display_value = getBlockParamDisplayText(block, ss_p_v, pname, catalog, controls);

                        let param_display_name = getBlockParamDisplayName(block, pname, catalog);

                        preset['snapshots'][ss_name]['params'][bname][param_display_name] = display_value;
                    }
                }
            }
        }
    }

    return preset;
}

function getPresetBlockPosition(preset, bname) {
    for (let i = 0; i < preset.blocks.length; i++) {
        if (preset.blocks[i]['block'] == bname) {
            return i;
        }
    }
    return undefined;
}

function buildSnapshots(preset, hlx, dsp = 'dsp0') {
    let preset_snapshots = {};
    //preset_snapshots['name'] = preset.name
    preset_snapshots['topology'] = preset.topology;
    preset_snapshots['blocks'] = clonedeep(preset.blocks);
    //preset_snapshots['snapshots'] = getSnapshotNames(hlx)

    for (let block_data of preset_snapshots['blocks']) {

        if (block_data['enabled'] != undefined) {
            let def_value = block_data['enabled'];
            block_data['enabled'] = { default: def_value };
        }
        if (block_data['params'] != undefined) {
            for (let p in block_data['params']) {
                let def_value = block_data['params'][p];
                block_data['params'][p] = { default: def_value };
            }
        }

        let xlink = undefined
        switch (block_data['block']) {
            case 'inputA':
                xlink = '#input-path0';
                if (block_data.dualpath) {
                    xlink += "-path1";
                }
                break;
            case 'split': xlink = '#split'; break;
            case 'join': xlink = '#join'; break;
            case 'outputA':
                xlink = '#output-path0';
                if (block_data.dualpath) {
                    xlink += "-path1";
                }
                break
            default:
                if (block_data['block'].includes('block')) {
                    xlink = '#block-path' + block_data.path;
                    if (block_data.fillpath != undefined) {
                        xlink += '-fill-path' + block_data.fillpath;
                    }
                }
                break
        }
        block_data['xlink'] = xlink;
    }

    for (let snap_name in preset.snapshots) {

        let snapshot_config = preset.snapshots[snap_name];
        for (let b in snapshot_config.blocks) {
            let block_data = getBlock(hlx, b, dsp);
            if (block_data != undefined) {
                if ((block_data['@no_snapshot_bypass'] != undefined && !block_data['@no_snapshot_bypass'])
                    || block_data['@no_snapshot_bypass'] == undefined) {

                    let preset_block_pos = getPresetBlockPosition(preset_snapshots, b);

                    if (preset_block_pos != undefined) {
                        preset_snapshots.blocks[preset_block_pos]['enabled'][snap_name] = snapshot_config.blocks[b];
                    }
                }
            }
        }
        if (snapshot_config.params != undefined) {
            for (let b in snapshot_config.params) {
                for (let p in snapshot_config.params[b]) {

                    let preset_block_pos = getPresetBlockPosition(preset_snapshots, b);

                    if (preset_block_pos != undefined) {
                        preset_snapshots.blocks[preset_block_pos]['params'][p][snap_name] = snapshot_config.params[b][p];
                    }
                }
            }
        }
    }

    return preset_snapshots;
}



function buildDualPresetDisplayModel(hlx) {
    let preset = {};
    preset['name'] = hlx.data.meta.name;
    let dsp0_display_model = buildPresetDisplayModel(hlx, 'dsp0', '@topology0');
    preset['dsp0'] = buildSnapshots(dsp0_display_model, hlx, 'dsp0');

    if (typeof hlx.data.tone.global["@topology1"] === 'string') {
        let dsp1_display_model = buildPresetDisplayModel(hlx, 'dsp1', '@topology1');
        preset['dsp1'] = buildSnapshots(dsp1_display_model, hlx, 'dsp1');
    } else {
        preset['dsp1'] = {};
    }

    preset['snapshots'] = getSnapshotNames(hlx);
    return preset;
}


/*********************/


function hlx2model(hlx) {
    let model = buildDualPresetDisplayModel(hlx);
    return model;
}



/*****************************/

function inflateSetlistToJson(json) {
	if(json.compression.type == 'zlib'
		&& json.encoding == 'Base64') {
		var buffer = Buffer.from(json.encoded_data, 'base64');
		//var hls_data = zlib.unzipSync(buffer).toString();
        var hls_data = pako.inflate(buffer, { to: 'string'});
		var hls_json = JSON.parse(hls_data);
		return hls_json;
	} else {
		console.log('Format not supported');
	}
	return nil;
}

function setlist2presets(setlist) {
	var presets = [];
	var cont = 1;
	var bank = 1;
	var bank_p = 1;
	for(let p of setlist.presets) {
		var l = '';
		if(bank_p==1) {
			l= 'A';
			if(cont>1) {
				bank++;
			}
		} else if(bank_p==2) {
			l='B';
		} else if(bank_p==3) {
			l='C';
		} else if(bank_p==0) {
			l='D';
		}
		
		if(p.meta != undefined) {
			var safe_name = p.meta.name.replace('\'','_').replace(':','_').replace('?','_').replace('/','_')
			var file_name = bank.toString().padStart(2, "0")+l+' '+safe_name
			//console.log(cont+' ' +file_name);
			p.meta.name = file_name;
			
			var np = {'data': p};
			//writeJSON(np, file_name+'.hlx');
			presets.push(np);
		}
		
		cont++;
		bank_p = cont%4;
	}
	return presets;
}

function hls2models(hls) {
	var setlist = inflateSetlistToJson(hls);
	var presets = setlist2presets(setlist);
	var models = [];
	for(let p of presets) {
		var model = hlx2model(p);
		models.push(model);
	}
	return models;
}




export { hlx2model, hls2models };