<template>
  <svg width="120px" height="140px" viewBox="0 0 120 140" xmlns="http://www.w3.org/2000/svg">
    <use v-bind="{'xlink:href':xlink}" :class="[category, isEnabled() ? 'active' : 'inactive']" />
  </svg>
</template>


<script>
export default {
  props: ["xlink", "category", "enabled", "snapshot"],
  methods: {
    isEnabled: function() {
      if (!this.enabled) {
        return false;
      }
      if (this.enabled[this.snapshot] != undefined) {
        return this.enabled[this.snapshot];
      }
      return this.enabled["default"];
    }
  }
};
</script>

