<template>
    <div>
        <div v-if="selection.length > 0" class="card d-lg-block co-12">
            <div class="card-body pb-0 pt-1">
                <h5 class="card-title">Files selected</h5>
                <h6 class="card-subtitle mb-2 text-muted">Click upload to process</h6>
            </div>
            <ul class="list-group list-group-flush">
                <li v-for="entry in selection" v-bind:key="entry.file.name"
                    class="list-group-item d-flex w-100 justify-content-between" :class="{
                        'text-danger': entry.state == 'error',
                        'text-success': entry.state == 'success',
                        'active': entry.state == 'processing'
                    }">
                    {{ entry.file.name }}
                    <span v-if="entry.msg">{{ entry.msg }}</span>
                </li>
            </ul>
            <div class="card-body pt-3 pb-3 text-center">
                <a v-if="!done" href="#" class="btn btn-primary" @click="uploadFiles">Upload files</a>
                <a v-else href="#" class="btn btn-primary" @click="reset">Finish</a>
            </div>
        </div>
        <div v-else class="dropzone p-4" v-on:click="openFileSelection" v-on:drop="onDrop" v-on:dragover="onDragOver"
            v-on:dragleave="onDragLeave" v-bind:class="{ highlight: highlight, disabled: !enabled }">
            <span class="align-middle">
                <h4>Add files</h4>
                <p>Drag and drop .hlx files here or click to open select file dialog</p>
                <p>6 nov 2022: Updated for HELIX 3.50</p>
                <p>Support for setlist: Drag and drop .hls files here or click to open select file dialog</p>
            </span>
            <input ref="fileInput" class="file-input" type="file" multiple v-on:change="onFileChanged" />
        </div>
    </div>
</template>

<script>
import { hlx2model, hls2models } from '../modules/hlxparser/hlxparser.js';

export default {
    props: ["enabled"],
    data: function () {
        return {
            highlight: false,
            selection: [],
            done: false,
            last_json: {}
        };
    },
    computed: {},
    methods: {
        openFileSelection: function () {
            if (!this.enabled) return;
            this.$refs.fileInput.click();
        },
        onDragOver: function (event) {
            if (!this.enabled) return;
            event.preventDefault();
            this.highlight = true;
        },
        onDragLeave: function (event) {
            this.highlight = false;
        },
        onDrop: function (event) {
            if (!this.enabled) return;
            event.preventDefault();
            let e_files = event.dataTransfer.files;
            this.selection = [];
            for (var i = 0; i < e_files.length; i++) {
                this.selection.push({
                    file: e_files.item(i),
                    state: "",
                    msg: undefined
                });
            }
            this.highlight = false;
        },
        onFileChanged: function (event) {
            let e_files = event.target.files;
            this.selection = [];
            for (var i = 0; i < e_files.length; i++) {
                this.selection.push({
                    file: e_files.item(i),
                    state: "",
                    msg: undefined
                });
            }
        },
        uploadFiles: function () {
            this.selection.map(selected => {
                selected.state = "processing";
                let file = selected.file;
                let lastdot = file.name.lastIndexOf(".");
                let extname = file.name.substring(lastdot);
                if (!((extname == ".hlx") || (extname == ".hls"))) {
                    selected.state = "error";
                    selected.msg = "Not .hlx or .hls file";
                    return true;
                }
                if (file.size > 1024 * 1024) {
                    selected.state = "error";
                    selected.msg = "Too big (> 1Mb)";
                    return true;
                }

                var reader = new FileReader();
                reader.onload = event => {
                    try {
                        let json = JSON.parse(event.target.result);
                        this.last_json = json;
                        let valid = false;
                        if (extname == ".hlx") {
                            if (json.data != undefined &&
                                json.data.device != undefined
                                && (json.data.device == "2162694"
                                    || json.data.device == "2162692"
                                    || json.data.device == "2162689"
                                    || json.data.device == "2162944"
                                    || json.data.device == "2162690"
                                    || json.data.device == "2162699")) {
                                valid = true;
                                var model = hlx2model(json);
                                this.$emit("new-preset", model);
                            }
                        }
                        if (extname == ".hls") {
                            if (json.meta != undefined &&
                                json.meta.device != undefined
                                && (json.meta.device == "2162694"
                                    || json.meta.device == "2162692"
                                    || json.meta.device == "2162689"
                                    || json.meta.device == "2162944"
                                    || json.data.device == "2162690"
                                    || json.data.device == "2162699")) {
                                valid = true;                                
                                let setlist = hls2models(json);
                                if (Array.isArray(setlist)) {
                                    this.$emit("new-setlist", setlist);
                                }
                            }
                        }
                        /*
                        if (valid) {
                            let formData = new FormData();
                            formData.append("uploads", selected.file);
                            //fetch("http://localhost:3000/upload-files", {
                            fetch("/upload-files", {
                                method: "POST",
                                //mode: "cors",
                                body: formData
                            })
                                .then(response => response.json())
                                .catch(error => {
                                    selected.state = "error";
                                    selected.msg = "Error uploading the file";
                                })
                                .then(response => {
                                    selected.state = "success";
                                    selected.msg = "Parsed correctly";
                                    if (extname == '.hlx') {
                                        this.$emit("new-preset", response[0]);
                                    } else if (extname == '.hls') {
                                        let setlist = response[0];
                                        if (Array.isArray(setlist)) {
                                            this.$emit("new-setlist", setlist);
                                        }
                                    }

                                });
                        } else {
                            selected.state = "error";
                            selected.msg = "not HX Stomp or Helix preset file";
                        }
                        */
                    } catch (error) {
                        selected.state = "error";
                        selected.msg = "format error reading .hlx file";
                    }
                };
                reader.onerror = error => {
                    selected.state = "error";
                    selected.msg = error;
                };
                reader.readAsText(selected.file);
            });
            this.done = true;
        },
        reset: function () {
            this.done = false;
            this.selection = [];
            this.$emit("done-loading");
        }
    }
};
</script>

<style scoped lang="scss">
.dropzone {
    position: relative;
    border: 3px dashed #2a9fd6;
    //color: #fff;
    //font: bold 24px/200px arial;
    min-height: 20em;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    cursor: pointer;
}

.highlight {
    background-color: #2387b7;
    border: 3px dashed #2180ac;
}

.disabled {
    border: 3px dashed grey;
    color: grey;
}

.file-input {
    display: none;
}
</style>