<template>
  <div>
    <div
      class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-4 pb-2 mb-3"
    >
      <div class="btn-toolbar mb-0 mb-md-0">
        <button
          v-if="!showList"
          @click="toggleShowList"
          type="button"
          class="btn btn-outline-primary btn-sm"
        >=</button>
      </div>
      <div class="btn-toolbar mb-2 mb-md-0">
        <h5>{{preset.name}}</h5>
      </div>
      <div class="btn-toolbar mb-2 mb-md-0">
        <div class="btn-group btn-group-sm mr-2" role="group">
          <button
            v-for="snapshot in preset.snapshots"
            v-bind:key="snapshot"
            type="button"
            @click="selectSnap(snapshot)"
            class="btn"
            :class="[current_snapshot == snapshot ? 'btn-primary' : 'btn-secondary']"
          >{{snapshot}}</button>
        </div>
      </div>
    </div>

    <div :class="[wideView ? 'patch-container-fill' : 'patch-container']">
      <!-- dsp0 -->
      <div class="patch">
        <div v-for="(block, index) in preset.dsp0.blocks" v-bind:key="index">
          <div v-if="block.xlink" class="patch_block">
            <block-svg
              :xlink="block.xlink"
              :category="block.category"
              :enabled="block.enabled"
              :snapshot="current_snapshot"
            ></block-svg>

            <div v-bind:class="['patch_block_description', 't-'+block.category]">
              <block-description :block="block" :snapshot="current_snapshot"></block-description>

              <template v-if="block.name == 'Input' && block.dualpath">
                <br />
                <block-description :block="getBlock('dsp0','inputB')" :snapshot="current_snapshot"></block-description>
              </template>

              <template v-if="block.cab">
                <br />
                <block-description
                  :block="getBlock('dsp0',block.cab)"
                  :snapshot="current_snapshot"
                ></block-description>
              </template>

              <template v-if="block.name == 'Output' && block.dualpath">
                <br />
                <block-description :block="getBlock('dsp0','outputB')" :snapshot="current_snapshot"></block-description>
              </template>
            </div>
          </div>
        </div>
      </div>

      <!-- dsp1 -->
      <div class="patch">
        <div v-for="(block, index) in preset.dsp1.blocks" v-bind:key="index">
          <div v-if="block.xlink" class="patch_block">
            <block-svg
              :xlink="block.xlink"
              :category="block.category"
              :enabled="block.enabled"
              :snapshot="current_snapshot"
            ></block-svg>

            <div v-bind:class="['patch_block_description', 't-'+block.category]">
              <block-description :block="block" :snapshot="current_snapshot"></block-description>

              <template v-if="block.name == 'Input' && block.dualpath">
                <br />
                <block-description :block="getBlock('dsp1','inputB')" :snapshot="current_snapshot"></block-description>
              </template>

              <template v-if="block.cab">
                <br />
                <block-description
                  :block="getBlock('dsp1',block.cab)"
                  :snapshot="current_snapshot"
                ></block-description>
              </template>

              <template v-if="block.name == 'Output' && block.dualpath">
                <br />
                <block-description :block="getBlock('dsp1','outputB')" :snapshot="current_snapshot"></block-description>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BlockSvg from "./BlockSvg.vue";
import BlockDescription from "./BlockDescription.vue";

export default {
  props: ["preset", "wideView", "showList"],
  data: function() {
    return {
      current_snapshot: "snapshot1"
    };
  },
  components: {
    BlockSvg,
    BlockDescription
  },
  methods: {
    selectSnap: function(sname) {
      this.current_snapshot = sname;
    },
    toggleShowList: function() {
      this.$emit("toggle-list");
    },
    getBlock: function(dsp, block_def) {
      for (let block of this.preset[dsp].blocks) {
        if (block.block == block_def) {
          return block;
        }
      }
      return undefined;
    }
  }
};
</script>

<style>
@media (min-width: 238px) {
  .patch-container {
    overflow: auto;
    max-width: 100vw;
  }

  .patch-container-fill {
    overflow: auto;
    max-width: 100vw;
  }
}

@media (min-width: 992px) {
  .patch-container {
    overflow: auto;
    max-width: calc(100vw - 340px);
  }

  .patch-container-fill {
    overflow: auto;
    max-width: calc(100vw);
  }
}

.patch {
  display: flex;
  flex-direction: row;
  margin-bottom: 4em;
}

.patch_block {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 120px;
  background: #060606;
}

.patch_block_description {
  color: grey;

  font-family: 'Muli',-system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  font-size: 12px;
  font-weight: 700;
  line-height: 1.8;
}

.amp {
  fill: #9a1612;
}
.t-amp {
  color: #9a1612;
}

.cab {
  fill: #9a1612;
}

.t-cab {
  color: #9a1612;
}

.distortion {
  fill: #a06c30;
}
.t-distortion {
  color: #a06c30;
}

.compressor {
  fill: #93902f;
}
.t-compressor {
  color: #93902f;
}

.gate {
  fill: #93902f;
}
.t-gate {
  color: #93902f;
}

.filter {
  fill: #ad46e2;
}
.t-filter {
  color: #ad46e2;
}

.pitch-synth {
  fill: #ad46e2;
}

.t-pitch-synth {
  color: #ad46e2;
}

.eq {
  fill: #93902f;
}
.t-eq {
  color: #93902f;
}

.looper {
  fill: #a0a0a0;
}

.t-looper {
  color: #a0a0a0;
}

.modulation {
  fill: #0094e9;
}
.t-modulation {
  color: #0094e9;
}

.delay {
  fill: #21811d;
}
.t-delay {
  color: #21811d;
}

.ir {
  fill: #9c3167;
}
.t-ir {
  color: #9c3167;
}

.reverb {
  fill: #aa491e;
}
.t-reverb {
  color: #aa491e;
}

.wah {
  fill: #ad46e2;
}
.t-wah {
  color: #ad46e2;
}

.sendreturn {
  fill: #a0a0a0;
}

.t-sendreturn {
  color: #a0a0a0;
}

.preamp {
  fill: #9a1612;
}
.t-preamp {
  color: #9a1612;
}

.volumepan {
  fill: #38a696;
}
.t-volumepan {
  color: #38a696;
}

.cabmicirs {
  fill: #9c3167;
}
.t-cabmicirs {
  color: #9c3167;
}


.active {
  stroke: #f2f3f4;
  stroke-width: 2;
}

.inactive {
  stroke: #555f61;
  stroke-width: 2;
}
</style>
