<template>
  <div id="app">
    <svg-defs></svg-defs>

    <b-navbar toggleable="lg" type="dark">
      <a class="navbar-brand" href="#">
        <img src="logo-yeraym-small.png" alt="HLX View" class="mr-2" />
        HLX View
        <span class="text-muted small">(by Yeray MQ)</span>
      </a>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <ul class="navbar-nav ml-auto">
          <li class="nav-item active">
            <a class="nav-link" href="#">
              Home
              <span class="sr-only">(current)</span>
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link" target="_blank" href="https://vulcanofx.blogspot.com/2019/08/helixhx-stomp-preset-snapshot-online.html">about</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" target="_blank" href="http://vulcanofx.blogspot.com/">blog</a>
          </li>
        </ul>
      </b-collapse>
    </b-navbar>

    <b-container fluid>
      <b-row>
        <!-- sidebar -->
        <!-- <div class="d-none col-12  mt-4 sidebar"> -->
        <div :class="[showList ? 'd-lg-block' : 'd-none']" class="col-12 mt-0 ml-0 sidebar">
          <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-4 pb-2 mb-3"
          >
            <button
              @click="toggleShowList"
              type="button"
              class="btn btn-outline-primary btn-sm"
              :disabled="loading"
            >=</button>
            <h5 class="font-weight-light">Presets</h5>
            <button @click="selectfiles" type="button" class="btn btn-outline-primary btn-sm">+</button>
          </div>
          <div class="card">
            <div class="card-body pt-1 pb-1 text-center">
              <button
              @click="clearList"
              type="button"
              class="btn btn-outline-primary btn-sm"
              :disabled="loading"
            >clear list</button>
            </div>
            <div class="list-group list-group-flush preset-list">
              <div
                class="list-group-item list-group-item-action d-flex w-100 justify-content-between"
                v-for="(pres, i) in presets"
                v-bind:key="pres.name"
                :class="{active: selected_preset==i}"
                @click="selectpreset(i)"
              >
                <span>{{pres.name}}</span>
                <button
                  type="button"
                  :class="[selected_preset==i ? 'btn-primary' : 'btn-light']"
                  class="btn btn-sm text-white-50"
                  @click.stop="removePreset(i)"
                >x</button>
              </div>
            </div>
            <div class="card-body pt-1 pb-1 text-center"></div>
          </div>
        </div>

        <!-- main -->
        <!-- <div class="d-none col-12"> -->
        <template v-if="loading">
          <div class="col-lg col-12">
            <div class="pt-3 pb-2 mb-3">
              <drop-zone
                :enabled="true"
                v-on:new-preset="onNewPreset"
                v-on:new-setlist="onNewSetlist"
                v-on:done-loading="doneloading"
              ></drop-zone>
            </div>
          </div>
        </template>

        <template v-else>
          <div class="col-lg col-12">
            <preset
              :preset="preset"
              :wide-view="!showList"
              v-on:toggle-list="toggleShowList"
              :showList="showList"
            ></preset>
          </div>
        </template>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import SvgDefs from "./components/SvgDefs.vue";
import DropZone from "./components/DopZone.vue";
import Preset from "./components/Preset.vue";

export default {
  name: "app",
  data: function() {
    return {
      loading: true,
      files: [],
      preset: null,
      showList: true,
      presets: [],
      selected_preset: 0,
      selected_presets: []
    };
  },
  components: {
    SvgDefs,
    DropZone,
    Preset
  },
  methods: {
    toggleShowList: function() {
      this.showList = !this.showList;
    },
    clearList: function() {
      this.presets = [];
      this.preset = undefined;
      this.selectfiles();
    },
    onNewPreset: function(_preset) {
      //console.log("onNewPrest");
      this.presets.push(_preset);
      if (!this.preset) this.preset = this.presets[0];
    },
    onNewSetlist: function(_setlist) {
      //console.log("onNewSetlist");
      if(Array.isArray(_setlist)) {
         for(let _preset of _setlist) {
           this.presets.push(_preset);
         }
         if (!this.preset) this.preset = this.presets[0];
      }
    },
    selectpreset: function(i) {
      this.selected_preset = i;
      this.preset = this.presets[i];
      this.loading = false;
      /*
      if (!this.selected_presets.includes(i)) {
        this.selected_presets.push(i);
      } else {
        let index = this.selected_presets.indexOf(i);
        if (index > -1) {
          this.selected_presets.splice(index, 1);
        }
      }*/
    },
    removePreset: function(i) {
      this.presets.splice(i, 1);
      if (i < this.selected_preset) {
        this.selected_preset--;
      } else if (this.selected_preset == i) {
        if (i == this.presets.length) {
          this.selected_preset = this.presets.length - 1;
        }
        this.preset = this.presets[this.selected_preset];
      }
      if (this.presets.length == 0) {
        this.preset = undefined;
        this.selectfiles();
      }
    },
    selectfiles: function() {
      this.files = [];
      this.loading = true;
    },
    doneloading: function() {
      if (this.presets.length > 0) {
        this.loading = false;
      } else {
        this.loading = true;
      }
    }
  }
};
</script>

