<template>
  <div :class="{snapparam: isSnapParam()}">{{name}}: {{getParamValue()}}</div>
</template>

<script>
export default {
  props: ["name", "value", "snapshot"],
  methods: {
    getParamValue: function() {
      if (this.value[this.snapshot]) {
        return this.value[this.snapshot];
      }
      return this.value["default"];
    },
    isSnapParam: function() {
      if (this.snapshot == "default") {
        return false;
      }
      if (this.value[this.snapshot]) {
        return true;
      }
      return false;
    }
  }
};
</script>

<style>
.snapparam {
  color: #dadedf;
}
</style>
